import React from 'react'
import s from './Dialogs.module.css'
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {Redirect} from "react-router-dom"
import {AddMessageReduxForm} from "./AddMessageForm/AddMessageReduxForm";
import {InitialStateType} from "../../redux/dialogs-reducer";

type PropsType = {
    messagesPage: InitialStateType
    sendMessage: (messageText: string) => void
}

export type NewMessageFormType = {
    newMessageText: string
}

const Dialogs: React.FC<PropsType> = (props) => {
    let dialogsElements = props.messagesPage.dialogs.map(dialog => (
        <DialogItem key={dialog.id} name={dialog.name} id={dialog.id}/>
    ))

    let messagesElements = props.messagesPage.messages.map(message => (
        <Message key={message.id} message={message.message}/>
    ))

    let onSubmit = (formData: NewMessageFormType) => {
        props.sendMessage(formData.newMessageText)
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                <div>
                    {messagesElements}
                </div>
                <AddMessageReduxForm onSubmit={onSubmit}/>
            </div>

        </div>
    )
}

export default Dialogs