import {UserType} from "../types/types";
import {InferActionTypes, ThunkType} from "./redux-store";
import {usersAPI} from "../api/users-api";


let initialState = {
    users: [] as Array<UserType>,
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [] as Array<number>,
    filter: {
        term: '',
        friend: null as null | boolean
    }
}

export type InitialStateType = typeof initialState


const usersReducer = (state = initialState, action: ActionsTypes): InitialStateType => {
    switch (action.type) {
        case 'FOLLOW':
            return {
                ...state,
                users: state.users.map(user => {
                    if (user.id === action.userId) {
                        return {...user, followed: true}
                    }
                    return user
                })
            }

        case 'UNFOLLOW':
            return {
                ...state,
                users: state.users.map(user => {
                    if (user.id === action.userId) {
                        return {...user, followed: false}
                    }
                    return user
                })
            }
        case 'SET_USERS':
            return {
                ...state,
                users: [...action.users]
            }
        case 'SET_CURRENT_PAGE':
            return {
                ...state,
                currentPage: action.currentPage
            }
        case 'SET_TOTAL_USERS_COUNT':
            return {
                ...state,
                totalUsersCount: action.totalCount
            }
        case 'TOGGLE_IS_FETCHING':
            return {
                ...state,
                isFetching: action.isFetching
            }
        case 'TOGGLE_IS_FOLLOWING_PROGRESS':
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : [...state.followingInProgress.filter(id => id !== action.userId)]
            }
        case 'SET_FILTER':
            return {
                ...state,
                filter: action.payload
            }
        default:
            return state
    }
}


export const getUsers = (currentPage: number, pageSize: number, filter: FilterType): ThunkType<ActionsTypes> => {
    return (dispatch, getState) => {
        dispatch(actions.toggleIsFetching(true))
        dispatch(actions.setFilter(filter))
        usersAPI.getUsers(currentPage, pageSize, filter.term, filter.friend)
            .then((data: any) => {
                dispatch(actions.setUsers(data.items))
                dispatch(actions.setTotalUsersCount(data.totalCount))
                dispatch(actions.toggleIsFetching(false))
            })
    }
}

export const follow = (userId: number): ThunkType<ActionsTypes> => {
    return async (dispatch) => {
        dispatch(actions.toggleFollowingProgress(true, userId))
        let data = await usersAPI.follow(userId)
        if (data.resultCode === 0) {
            dispatch(actions.followSuccess(userId))
            dispatch(actions.toggleFollowingProgress(false, userId))
        }
    }
}

export const unfollow = (userId: number): ThunkType<ActionsTypes> => {
    return (dispatch) => {
        dispatch(actions.toggleFollowingProgress(true, userId))
        usersAPI.unfollow(userId)
            .then((data: any) => {
                if (data.resultCode === 0) {
                    dispatch(actions.unfollowSuccess(userId))
                    dispatch(actions.toggleFollowingProgress(false, userId))
                }
            })
    }
}

type ActionsTypes = InferActionTypes<typeof actions>
export type FilterType = typeof initialState.filter

export const actions = {
    followSuccess: (userId: number) => {
        return {
            type: 'FOLLOW',
            userId: userId
        } as const
    },
    unfollowSuccess: (userId: number) => {
        return {
            type: 'UNFOLLOW',
            userId: userId
        } as const
    },
    setUsers: (users: Array<UserType>) => {
        return {
            type: 'SET_USERS',
            users: users
        } as const
    },
    setTotalUsersCount: (totalCount: number) => {
        return {
            type: 'SET_TOTAL_USERS_COUNT',
            totalCount: totalCount
        } as const
    },
    setCurrentPage: (currentPage: number) => {
        return {
            type: 'SET_CURRENT_PAGE',
            currentPage: currentPage
        } as const
    },
    toggleIsFetching: (isFetching: boolean) => {
        return {
            type: 'TOGGLE_IS_FETCHING',
            isFetching: isFetching
        } as const
    },
    toggleFollowingProgress: (isFetching: boolean, userId: number) => {
        return {
            type: 'TOGGLE_IS_FOLLOWING_PROGRESS',
            isFetching,
            userId
        } as const
    },
    setFilter: (filter: FilterType) => {
        return {
            type: 'SET_FILTER',
            payload: filter
        } as const
    }
}

export default usersReducer