import {stopSubmit} from "redux-form";
import {PhotosType, PostType, ProfileType} from "../types/types";
import {profileAPI as ProfileApi} from "../api/profile-api";
import {InferActionTypes, ThunkType} from "./redux-store";

let initialState = {
    posts: [
        {id: 1, text: 'Sveta', likesCount: 1},
        {id: 2, text: 'Sasha', likesCount: 3},
        {id: 3, text: 'Dima', likesCount: 5},
        {id: 4, text: 'Kolya', likesCount: 6},
        {id: 5, text: 'Artem', likesCount: 7},
        {id: 6, text: 'Vika', likesCount: 8}
    ] as Array<PostType>,
    profile: null as ProfileType | null,
    status: "",
}

export type InitialStateType = typeof initialState

const profileReducer = (state = initialState, action: ActionsType): InitialStateType => {
    switch (action.type) {
        case 'SN/PROFILE/ADD-POST': {
            let newPost = {
                id: 7,
                text: action.newPostText,
                likesCount: 0
            }
            return {
                ...state,
                posts: [...state.posts, newPost]
            }
        }
        case 'SN/PROFILE/SET-USER-PROFILE': {
            return {
                ...state,
                profile: action.profile
            }
        }
        case 'SN/PROFILE/SET-STATUS':
            return {
                ...state,
                status: action.status
            }
        case 'SN/PROFILE/SET-PHOTO-SUCCESS':
            return {
                ...state,
                profile: {...state.profile, photos: action.photos} as ProfileType
            }
        default:
            return state
    }
}

export const actions = {
    addPostActionCreator: (newPostText: string) => {
        return {
            type: 'SN/PROFILE/ADD-POST',
            newPostText
        } as const
    },
    setUserProfile: (profile: ProfileType) => {
        return {
            type: 'SN/PROFILE/SET-USER-PROFILE',
            profile
        } as const
    },
    setStatus: (status: string) => {
        return {
            type: 'SN/PROFILE/SET-STATUS',
            status
        } as const
    },
    savePhotoSuccess: (photos: PhotosType) => {
        return {
            type: 'SN/PROFILE/SET-PHOTO-SUCCESS',
            photos
        } as const
    }
}

export const getUserProfile = (userId: number | null): ThunkType<ActionsType> => {
    return async (dispatch) => {
        let data = await ProfileApi.getProfile(userId)
        dispatch(actions.setUserProfile(data))
    }
}

export const getStatus = (userId: number): ThunkType<ActionsType> => {
    return (dispatch) => {
        ProfileApi.getStatus(userId)
            .then((response: any) => {
                dispatch(actions.setStatus(response))
            })
    }
}

export const updateStatus = (status: string): ThunkType<ActionsType> => {
    return (dispatch) => {
        ProfileApi.updateStatus(status)
            .then((response: any) => {
                if (response.data.resultCode === 0) {
                    dispatch(actions.setStatus(status))
                }
            })
    }
}

export const savePhoto = (file: File): ThunkType<ActionsType> => {
    return async (dispatch) => {
        let data = await ProfileApi.savePhoto(file)
        if (data.resultCode === 0) {
            dispatch(actions.savePhotoSuccess(data.data.photos))
        }
    }
}

export const saveProfile = (profile: ProfileType): ThunkType<ActionsType | ReturnType<typeof stopSubmit>> => {
    return async (dispatch, getState) => {
        const userId = getState().auth.userId
        let data = await ProfileApi.saveProfile(profile)
        if (data.resultCode === 0) {
            dispatch(getUserProfile(userId))
        } else {
            dispatch(stopSubmit('editProfile', {
                _error: data.messages[0]
            }))
        }
    }
}

type ActionsType = InferActionTypes<typeof actions>

export default profileReducer