import {Field, InjectedFormProps, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {createField, Textarea} from "../../common/FormsControls/FormsControls";
import React from "react";
import {NewMessageFormType} from "../Dialogs";

const maxLength50 = maxLengthCreator(50)

type NewMessageFormTypeKeys = Extract<keyof NewMessageFormType, string>

const AddMessageForm: React.FC<InjectedFormProps<NewMessageFormType>> = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                {createField<NewMessageFormTypeKeys>("Enter new message text", "newMessageText", [required, maxLength50], Textarea)}
            </div>
            <div>
                <button></button>
            </div>
        </form>
    )
}
export const AddMessageReduxForm = reduxForm<NewMessageFormType>({
    form: 'dialogs',
})(AddMessageForm)