import {Field, InjectedFormProps, reduxForm} from "redux-form";
import {createField, GetStringKeys, Textarea} from "../../../common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../../../utils/validators/validators";
import React from "react";
import {NewMessageFormType} from "../../../Dialogs/Dialogs";

const maxLength10 = maxLengthCreator(10)

type PropsType = {

}

export type AddPostFormValuesType = {
    newPostText: string
}

type AddPostFormTypeKeys = GetStringKeys<AddPostFormValuesType>

const PostsForm: React.FC<InjectedFormProps<AddPostFormValuesType>> = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                {createField<AddPostFormTypeKeys>("\"Enter new post text", "newPostText", [required, maxLength10], Textarea)}

            </div>
            <div>
                <button>Add post</button>
            </div>
        </form>
    )
}
export const PostsReduxForm = reduxForm<AddPostFormValuesType, {}>({
    form: 'posts',
})(PostsForm)