import {InferActionTypes} from "./redux-store";

type DialogType = {
    id: number
    name: string
}

type MessageType = {
    id: number
    message: string
}

let initialState = {
    dialogs: [
        {id: 1, name: 'Sveta'}, {id: 2, name: 'Sasha'},
        {id: 3, name: 'Dima'}, {id: 4, name: 'Kolya'},
        {id: 5, name: 'Artem'}, {id: 6, name: 'Vika'}
    ] as Array<DialogType>,
    messages: [
        {id: 1, message: 'Hello'}, {id: 2, message: 'Hi'},
        {id: 3, message: 'How old are you?'}, {id: 4, message: 'I\'m 26 yo'},
        {id: 5, message: 'Are you here?'}, {id: 6, message: 'Yes'}
    ] as Array<MessageType>,
}

export type InitialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: ActionsType): InitialStateType => {
    switch (action.type) {
        case 'SN/dialogs/ADD-MESSAGE': {
            let newMessage = {
                id: 7,
                message: action.newMessageText,
            }
            return {
                ...state,
                messages: [...state.messages, newMessage]
            }
        }
        default:
            return state
    }

}


type ActionsType = InferActionTypes<typeof actions>

export const actions = {
    sendMessage: (newMessageText: string) => {
        return {
            type: 'SN/dialogs/ADD-MESSAGE',
            newMessageText
        } as const
    }
}

export default dialogsReducer