import React, {ChangeEvent, useState} from 'react';
import s from './ProfileInfo.module.css'
import Preloader from "../../common/Preloader/Preloader";
import userPhoto from "../../../assets/images/default_user.png";
import ProfileStatusWithHooks from "./ProfileStausWithHooks";
import ProfileDataForm from "./ProfileDataForm";
import {ContactsType, ProfileType} from "../../../types/types";

type PropsType = {
    profile: ProfileType | null
    status: string
    saveProfile: (profile: ProfileType) => void
    savePhoto: (photoFile: File) => void
    isOwner: boolean
    updateStatus: (status: string) => void
}

const ProfileInfo: React.FC<PropsType> = (props) => {

    let [editMode, setEditMode] = useState(false)

    if (!props.profile) {
        return <Preloader/>
    }

    let onSubmit = (formData: ProfileType) => {
        props.saveProfile(formData)
        setEditMode(false)
    }

    const onMainPhotoSelected = (e: ChangeEvent<HTMLInputElement>) => {
        if(e.target.files?.length) {
            props.savePhoto(e.target.files[0])
        }
    }

    return (
        <div className={s.content}>
            {/*<div>*/}
            {/*    <img className={s.album}*/}
            {/*         src="https://media-exp1.licdn.com/dms/image/C4D1BAQGDmALg_8s-Yg/company-background_10000/0/1519799119530?e=2159024400&v=beta&t=4WV9YKR9L3PAEnppWmPPMk5xVnETtWvhZN8NexEzPwM"*/}
            {/*         alt=""/>*/}
            {/*</div>*/}
            <div className={s.descriptionBlock}>
                <img className={s.album}
                     src={props.profile.photos.large || userPhoto}
                     alt=""/>
                { props.isOwner && <input type="file" onChange={onMainPhotoSelected}/>}
                { editMode ? <ProfileDataForm initialValues={props.profile} onSubmit={onSubmit} profile={props.profile}/> : <ProfileData profile={props.profile} isOwner={props.isOwner} goToEditMode={() => {setEditMode(true)}}/>}
            </div>
            <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
        </div>
    );
}

type ProfileDataPropsType = {
    isOwner: boolean
    profile: ProfileType
    goToEditMode: () => void
}

const ProfileData: React.FC<ProfileDataPropsType> = (props) => {
    return(
        <div>
            { props.isOwner && <div><button onClick={props.goToEditMode}>Edit</button></div>}
            <div>
                <b>Full name: </b>{ props.profile.fullName }
            </div>
            <div>
                <b>Looking for a job: </b>{ props.profile.lookingForAJob ? "yes" : 'No' }
            </div>
            { props.profile.lookingForAJob && <div>
                <b>My professional skills: {props.profile.lookingForAJobDescription}</b>
            </div>}
            <div>
                <b>About me: </b>{props.profile.aboutMe}
            </div>
            <div>
                <b>Contacts: </b>{Object.keys(props.profile.contacts).map((key) => <Contact key={key} contactTitle={key} contactValue={props.profile.contacts[key as keyof ContactsType]}/>)}
            </div>
        </div>
    )
}

type ContactsPropsType = {
    contactTitle: string
    contactValue: string
}

const Contact: React.FC<ContactsPropsType> = ({contactTitle, contactValue}) => {
    return(
        <div className={s.contact}>
            <b>{contactTitle}:</b> {contactValue}
        </div>
    )
}

export default ProfileInfo;