import React from 'react';
import s from './MyPosts.module.css'
import Post from "./Post/Post"
import {AddPostFormValuesType, PostsReduxForm} from "./AddPostForm/PostsReduxForm";
import {PostType} from "../../../types/types";

export type MapPropsType = {
    posts: Array<PostType>
}

export type DispatchPropsType = {
    addPost: (newPostText: string) => void
}

const MyPosts: React.FC<MapPropsType & DispatchPropsType> = (props) => {

    let postsElements = props.posts.map(post => <Post key={post.id} message={post.text} likesCount={post.likesCount}/>)

    let onSubmit = (formData: AddPostFormValuesType) => {
        props.addPost(formData.newPostText)
    }

    return (
        <div className={s.postsBlock}>
            <h3>My post</h3>
            <PostsReduxForm onSubmit={onSubmit}/>
            <div className={s.posts}>
                {postsElements}
            </div>
        </div>
    );
}

export default MyPosts;