import {PhotosType, ProfileType} from "../types/types";
import {APIInstance, ResponseType} from "./api";

type SavePhotoResponseDataType = {
    photos: PhotosType
}

export const profileAPI = {
    getProfile(userId: number | null) {
        return APIInstance
            .get<ProfileType>(`profile/${userId}`).then(response => response.data)
    },
    getStatus(userId: number) {
        return APIInstance
            .get<string>(`profile/status/${userId}`).then(response => response.data)
    },
    updateStatus(status: string) {
        return APIInstance
            .put<ResponseType>(`profile/status`, {
                status: status
            }).then(response => response.data)
    },
    savePhoto(photoFile: File) {
        const formData = new FormData()
        formData.append('image', photoFile)
        return APIInstance
            .put<ResponseType<SavePhotoResponseDataType>>(`profile/photo`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }).then(response => response.data)
    },
    saveProfile(profile: ProfileType) {
        return APIInstance
            .put<ResponseType<ProfileType>>(`profile`, profile).then(response => response.data)
    }
}