import {ResultCodesEnum} from "../api/api";
import {stopSubmit} from "redux-form";
import {InferActionTypes, ThunkType} from "./redux-store";
import {authAPI} from "../api/auth-api";

let initialState = {
    userId: null as number | null,
    email: null as string | null,
    login: null as string | null,
    isAuth: false
    // isFetching: false
}

export type InitialStateType = typeof initialState

const authReducer = (state = initialState, action: ActionsTypes): InitialStateType => {
    switch (action.type) {
        case 'SN/AUTH/SET-USER-DATA':
            return {
                ...state,
                ...action.data,
                isAuth: action.data.isAuth
            }
        default:
            return state
    }
}



export const actions = {
    setAuthUserDataSuccess: (userId: null | number, email: null | string, login: null | string, isAuth: boolean) => {
        return {
            type: 'SN/AUTH/SET-USER-DATA',
            data: {
                userId: userId,
                email: email,
                login: login,
                isAuth
            }
        } as const
    }
}

type ActionsTypes = InferActionTypes<typeof actions>

export const setAuthUserData = (): ThunkType<ActionsTypes> => {
    return async (dispatch) => {
        let data = await authAPI.getMe()
        if (data.resultCode === ResultCodesEnum.Success) {
            let {id, login, email} = data.data
            dispatch(actions.setAuthUserDataSuccess(id, email, login, true))
        }
    }
}

export const login = (email: null | string, password: null | string, rememberMe: boolean): ThunkType<ActionsTypes | ReturnType<typeof stopSubmit>> => {
    return async (dispatch) => {
        let data = await authAPI.login(email, password, rememberMe)

        if (data.resultCode === ResultCodesEnum.Success) {
            dispatch(setAuthUserData())
        } else {
            let message = data.messages.length > 0 ? data.messages[0] : "Some error"
            dispatch(stopSubmit('login', {
                _error: message
            }))
        }
    }
}

export const logout = (): ThunkType<ActionsTypes> => {
    return async (dispatch) => {
        let data = await authAPI.logout()
        if (data.resultCode === ResultCodesEnum.Success) {
            dispatch(actions.setAuthUserDataSuccess(null, null, null, false))
        }

    }
}

export default authReducer