import {APIInstance, ResponseType} from "./api";

type GetMeResponseDataType = {
    id: number
    login: string
    email: string
}
type LoginResponseDataType = {
    userId: number
}
type LogoutResponseDataType = {}
export const authAPI = {
    getMe() {
        return APIInstance
            .get<ResponseType<GetMeResponseDataType>>(`auth/me`)
            .then(response => response.data)
    },
    login(email: null | string, password: null | string, rememberMe: boolean) {
        return APIInstance
            .post<ResponseType<LoginResponseDataType>>('auth/login', {
                email,
                password,
                rememberMe
            })
            .then(response => response.data)
    },
    logout() {
        return APIInstance
            .delete<ResponseType<LogoutResponseDataType>>('auth/login')
            .then(response => response.data)
    }
}